import React, { useState } from "react"

const AddPlayer = ({ addPlayer }) => {
    const initial = { id: null, name: "", club: "", important: false }
    const [player, setPlayer] = useState(initial)

    const change = e => {
        const { name, value } = e.target
        setPlayer({ ...player, [name]: value })
    }

    const submit = e => {
        e.preventDefault()
        if (!player.name || !player.club) return
        addPlayer(player)
        setPlayer(initial)
    }

    return (
        <form onSubmit={submit}>
            <h2>Add player</h2>
            <input
                placeholder="Enter name"
                onChange={change}
                name="name"
                value={player.name}
            />
            <input
                placeholder="Enter club"
                onChange={change}
                name="club"
                value={player.club}
            />
            <button>Add player</button>
        </form>
    )
}

export default AddPlayer
