import axios from 'axios'
const baseURL = '/players'

const getAllService = () => {
    const request = axios.get(baseURL)
    return request.then(response => response.data)
}

const deletePlayerService = id => {
    const request = axios.delete(`${baseURL}/${id}`)
    return request.then(response => response.data)
}

const createPlayerService = obj => {
    const request = axios.post(baseURL, obj)
    return request.then(response => response.data)
}

const editPlayerService = (id, obj) => {
    const request = axios.put(`${baseURL}/${id}`, obj)
    return request.then(response => response.data)
}

const togglePlayerService = (id, obj) => {
    const request = axios.patch(`${baseURL}/${id}`, obj)
    return request.then(response => response.data)
}

export default {getAllService, deletePlayerService, createPlayerService, editPlayerService, togglePlayerService}