import React, { useState, useEffect } from 'react'
import DisplayPlayers from './components/DisplayPlayers'
import EditPlayer from './components/EditPlayer'
import AddPlayer from './components/AddPlayer'
import Services from './services/players'

const App = () => {
  const [editing, setEditing] = useState(false)
  const [playerToEdit, setPlayerToEdit] = useState()
  const [players, setPlayers] = useState([])


  const addPlayer = obj => {
    Services
      .createPlayerService(obj)
      .then(data => {
        setPlayers(players.concat(data))
      })
  }

  const deletePlayer = id => {
    Services
      .deletePlayerService(id)
      .then(data => {
        setPlayers(players.filter(p => p.id !== id))
      })
  }

  const whichPlayer = obj => {
    setEditing(true);
    setPlayerToEdit(obj);
  };

  const editPlayer = (id, obj) => {
    Services
      .editPlayerService(id, obj)
      .then(data => {
        setPlayers(players.map(p => p.id === id ? data : p))
        setEditing(false)
      })
  };

  const toggle = (id, obj) => {
    Services
      .togglePlayerService(id, obj)
      .then(data => {
        setPlayers(players.map(p => p.id === id ? data : p))
      })
  };

  useEffect(() => {
    Services
      .getAllService()
      .then(data => setPlayers(data))
  }, [])

  return (
    <div className="center">
      <h1>Practicing</h1>
      <hr />

      {editing ?
        <EditPlayer
          setEditing={setEditing}
          playerToEdit={playerToEdit}
          editPlayer={editPlayer}
        /> :
        <AddPlayer
          addPlayer={addPlayer}
        />}

      <DisplayPlayers
        players={players}
        deletePlayer={deletePlayer}
        whichPlayer={whichPlayer}
        toggle={toggle}
      />
    </div>
  )
}

export default App
